<template>
  <div
    id="show-blog"
    v-theme:column="'narrow'"
  >
    <h1>博客总览</h1>
    <input
      type="text"
      v-model="search"
      placeholder="搜索"
    >
    <div
      class="single-blog"
      v-for="blog in filteredBlogs"
    >
     <router-link v-bind:to="'/blog/'+blog.id"><h2 v-rainbow>{{blog.title | to-uppercase}}</h2></router-link> 
      <article>
        {{blog.body | snippet}}
      </article>
    </div>
  </div>
</template>

<script>

export default {
  name: 'show-blog',
  data() {
    return {
      blogs: [],
      search: ""
    }
  },
  created() {
    this.$http.get('http://jsonplaceholder.typicode.com/posts')
      .then(function (data) {
        // console.log(data);
        this.blogs = data.body.slice(0, 10);
        console.log(this.blogs);
      })
  },
  computed: {
    filteredBlogs: function () {
      return this.blogs.filter((blog) => {
        return blog.title.match(this.search)
      })
    }
  }
}

</script>

<style>
#show-blog {
  max-width: 800px;
  margin:0 auto;
}
.single-blog {
  padding: 20px;
  margin: 20px 0;
  box-sizing: border-box;
  background: #eee;
  border: 1px dotted #aaa;
}
#show-blog a{
  color: #444;
  text-decoration: none;
}
input[type="text"]{
  padding: 8px;
  width: 100%;
  box-sizing: border-box;
}
</style>



