<template>
  <div id="add-blog">
    <h1>添加博客</h1>
    <form v-if="!submmited">
      <label>博客标题</label>
      <input
        type="text"
        v-model="blog.title"
        required
      />

      <label>博客内容</label>
      <textarea
        type="text"
        v-model="blog.conten"
        required
      />
      <div id="checkboxes">
        <label >Vue.js</label>
        <input type="checkbox" value="Vue.js" v-model="blog.add">
        <label >Node.js</label>
        <input type="checkbox" value="Node.js" v-model="blog.add">
        <label >React.js</label>
        <input type="checkbox" value="React.js" v-model="blog.add">
        <label >Angular4.js</label>
        <input type="checkbox" value="Angular4.js" v-model="blog.add">
      </div>
      <label >作者：</label>
      <select v-model="blog.author" >
        <option v-for="author in authors">{{author}}</option>
      </select>
      <button @click.prevent="post">添加博客</button>
      </form>

      <div v-if="submmited">
        <h3>您的博客发布成功！</h3>
      </div>
      <div class="preview">
        <h3>博客总览</h3>
        <p>博客标题：{{blog.title}}</p>
        <p>博客内容:</p>
        <p>{{blog.conten}}</p>
        <p>博客分类：</p>
        <ul>
          <li v-for="type in blog.add">
            {{type}}
          </li>
        </ul>
        <p>作者：{{blog.author}}</p>
      </div>
  </div>
</template>

<script>
export default {
  name: 'add-blog',
  data() {
    return {
      blog:{
        title:"",
        conten:"",
        add:[],
        author:""
      },
      authors:["Hu","Huang","Xie"],
      submmited:false
    }
  },
  methods:{
    post:function(){
      this.$http.post("http://jsonplaceholder.typicode.com/posts",{
        title:this.blog.title,
        body:this.blog.conten,
        userId:1
      })
      .then(function(data){
        console.log(data);
        this.submmited =true
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#add-blog *{
  box-sizing: border-box;
}
#add-blog{
  margin: 20px auto;
  max-width: 600px;
  padding: 20px;
}
label{
  display: block;
  margin: 20px 0 10px;
}
input[type="text"],textarea,select{
  display: block;
  width: 100%;
  padding: 8px;
}
#checkboxes label{
  display: inline-block;
  margin-top: 0;
}
#checkboxes input{
  display: inline-block;
  margin-right: 10px;
}
button{
  display: block;
  margin: 20px 0;
  background: red;
  color: #fff;
  border: 0;
  padding: 14px;
  border-radius: 4px;
  font-size: 18px;
  cursor: pointer;
}
preview{
  padding: 10px 20px;
  border: 1px dotted #ccc;
  margin: 30px 0; 
}
h3{
  margin-top: 10px;
}
textarea{
  height: 200px;
}
</style>



